	module.exports = {"data":{"markdownRemark":{"html":"<p>This post is written by Jan Hein Hoogstad. Thanks to 42workspace for sponsoring the meetup.</p>\n<p>The theme of this month’s Serverless Meetup Rotterdam was MQTT. Thomas van der Veen took the lead in presenting a talk on this topic. The second speaker, Shannon Dealy, who planned to talk about Servers &#x26; Serverless, unfortunately had to cancel due to flu. As we had to compete with the Serverlessconf in Paris and high time for winter holidays, we had a slightly smaller group than at our previous meetups. No problem there, we held an unconference with the group on topics they liked to discuss: problem-solving in software development and the programming language Go.</p>\n<p>Keep an eye on our Meetup page for upcoming dates and speakers, in which among others Shannon eventually will get a chance to present his talk too.</p>","frontmatter":{"date":"March 10, 2018","path":"/blog/charm","title":"Three strikes","featuredImage":{"childImageSharp":{"resolutions":{"base64":"data:image/jpeg;base64,","width":900,"height":300,"src":"/offcourse-website/static/graphql-cb65aa77f04c98250d91cfd0460351f2-f5803.jpg","srcSet":"/offcourse-website/static/graphql-cb65aa77f04c98250d91cfd0460351f2-f5803.jpg 1x"}}}}}},"pathContext":{}}

